<template>
    <div>
        <h2>数据显示</h2>
        <el-row justify="center" type="flex">
            <el-col :span="20">
                <el-row>
                    <el-col :span="8">
                        <el-image :src="imgUrl" fit="fill" style="width: 250px;height: 400px;"/>
                    </el-col>
                    <el-col :span="8">
                        <el-row style="margin-top: 10px;" v-for="(item , index) in angle">轴{{index + 1}}： {{item}}度
                        </el-row>
                        <el-row style="margin-top: 10px;">
                            <el-button @click="getAngle">获取角度</el-button>
                        </el-row>
                    </el-col>
                    <el-col :span="8">
                        <div>
                            <h3>位置历史</h3>
                            <el-timeline :reverse="true" style="margin-top: 20px;">
                                <el-timeline-item
                                        v-for="(activity, index) in activities"
                                        :key="index"
                                        :timestamp="activity.timestamp">
                                    {{activity.content}}
                                </el-timeline-item>
                            </el-timeline>
                        </div>
                    </el-col>
                </el-row>
            </el-col>
        </el-row>
    </div>
</template>

<script>
    export default {
        name: "DataShow",
        data() {
            return {
                imgUrl: require("@/assets/armPic.png"),
                activities: [],
                angle: [0, 0, 0, 0, 0, 0]
            }
        },
        mounted() {
            this.getArmPositionHistory();
        },
        methods: {
            getArmPositionHistory() {
                this.$ajax.get('/armControl/positionHistory').then((res) => {
                    this.activities = []
                    for (let item of res.data) {
                        this.activities.push({"timestamp": item.timestamp, "content": item.description})
                    }
                })
                const that = this;
                this._interval = setTimeout(function () {
                    that.getArmPositionHistory()
                }, 1000)
            },

            getAngle(){
                this.angle = []
                this.$ajax.get('/armControl/angle').then((res) => {
                    for (let item of res.data) {
                        this.angle.push(item)
                    }
                })
            }
        }
    }
</script>

