<template>
<div>
    <h2>数字显示控制</h2>
    <div style="margin-top: 40px;">
        <el-row justify="center" type="flex" style="margin-bottom: 10px;">
            <el-col :span="20">
                <el-select v-model="value" placeholder="请选择显示的数字" value="">
                    <el-option
                            v-for="item in options"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">
                    </el-option>
                </el-select>
            </el-col>
        </el-row>
        <el-row>
            <el-col>
                <el-button type="primary" @click="sendDigital">确认并发送</el-button>
            </el-col>
        </el-row>
    </div>
</div>
</template>

<script>
    export default {
        name: "DigitalControl",
        data(){
            return{
                options:[
                    {
                        "value": "1",
                        "label": "数字1"
                    },
                    {
                        "value": "2",
                        "label": "数字2"
                    },
                    {
                        "value": "3",
                        "label": "数字3"
                    },
                    {
                        "value": "4",
                        "label": "数字4"
                    },
                    {
                        "value": "5",
                        "label": "数字5"
                    },
                    {
                        "value": "6",
                        "label": "数字6"
                    },
                    {
                        "value": "7",
                        "label": "数字7"
                    },
                    {
                        "value": "8",
                        "label": "数字8"
                    },
                    {
                        "value": "9",
                        "label": "数字9"
                    },
                    {
                        "value": "0",
                        "label": "数字0"
                    },
                ],
                value: "1"
            }
        },
        methods:{
            sendDigital(){
                this.$ajax.post('/armControl/digitalControl',{
                    'showNumber': this.value
                }).then((res) =>{
                    console.log(res)
                    this.$notify({
                        title: '发送成功',
                        message: '指令发送成功，请查看主机'
                    })
                }).catch((err) =>{
                    console.error(err)
                })
            }
        }
    }
</script>

<style scoped>

</style>
