<template>
<div>
    <h2>远程控制</h2>
    <el-row class="btn-container">
        <el-col :span="12"><el-button type="primary" @click="sendControl('单次抓取')">单次抓取</el-button></el-col>
        <el-col :span="12"><el-button type="primary" @click="sendControl('连续抓取')">连续抓取</el-button></el-col>
    </el-row>
    <el-row class="btn-container">
        <el-col :span="12"><el-button type="primary" @click="sendControl('开始/暂停')">开始/暂停动作</el-button></el-col>
        <el-col :span="12"><el-button type="primary" @click="sendControl('复位')">复位</el-button></el-col>
    </el-row>
    <el-row class="btn-container">
        <el-col :span="12"><el-button type="primary" @click="sendControl('开传送带')">开传送带</el-button></el-col>
        <el-col :span="12"><el-button type="primary" @click="sendControl('关传送带')">关传送带</el-button></el-col>
    </el-row>
    <el-row class="btn-container">
        <el-col><el-button type="warning" @click="sendControl('切换控制')">切换控制至本地</el-button></el-col>
    </el-row>
</div>
</template>

<script>
    export default {
        name: "RemoteControl",
        data(){},
        methods:{
            sendControl(controlType) {
                this.$ajax.post('/armControl/remoteControl',{
                    'controlType': controlType
                }).then((res)=>{
                    console.log(res)
                    this.$notify({
                        title: '发送成功',
                        message: '指令发送成功，请查看主机'
                    })
                }).catch((err) =>{
                    console.log(err)
                })
            }
        }
    }
</script>

<style scoped>
.el-row{
    margin-bottom: 10px;
}
</style>
