<template>
    <div>
        <h2>轴控制</h2>
        <div style="margin-top: 100px;">
            <el-row justify="center" type="flex" style="margin-bottom: 30px;">
                <el-col :span="12">
                    <el-select v-model="value" placeholder="请选择" value="">
                        <el-option
                                v-for="item in options"
                                :key="item.value"
                                :label="item.label"
                                :value="item.value">
                        </el-option>
                    </el-select>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="12"><el-button type="primary" @click="sendAxisControl(true)">顺时针旋转</el-button></el-col>
                <el-col :span="12"><el-button type="primary" @click="sendAxisControl(false)">逆时针旋转</el-button></el-col>
            </el-row>
        </div>
    </div>
</template>

<script>
    export default {
        name: "AxisControl",
        data() {
            return {
                options: [{
                    value: '1',
                    label: '轴1'
                }, {
                    value: '2',
                    label: '轴2'
                }, {
                    value: '3',
                    label: '轴3'
                }, {
                    value: '4',
                    label: '轴4'
                }, {
                    value: '5',
                    label: '轴5'
                }],
                value: '1'
            }
        },
        methods:{
            sendAxisControl(clockwise){
                this.$ajax.post('/armControl/axisControl',{
                    'axisNum': this.value,
                    'clockwise': clockwise
                }).then((res)=>{
                    console.log(res)
                    this.$notify({
                        title: '发送成功',
                        message: '指令发送成功，请查看主机'
                    })
                }).catch((err) =>{
                    console.log(err)
                })
            }
        }
    }
</script>

<style scoped>

</style>
